ir las indicaciones del plan de recopilación de información de datos de energía desarrollada en la etapa de planificación.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/tema2-05.png"),"alt":""}})])])]),_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('h4',[_vm._v("c. Cálculo de la línea de base energética")]),_c('p',[_vm._v("Dependiendo del tipo de IDEn identificado, se procede a realizar el cálculo de la línea de base energética para cada uno de ellos, y si se requiere o no de un modelo estadístico.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema2/tema2-06.png"),"alt":""}})])])])])],1),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"flip-down"}},[_vm._v(" A continuación, se muestran los dos ejemplos de modelos más comúnmente utilizados:")]),_vm._m(1),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"flip-right"}},[_vm._v("Para la construcción de la línea de base energética, se cuenta con el registro de las facturas de los últimos doce meses con la siguiente información:")]),_vm._m(2),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("De acuerdo con la tabla y figura anterior, se puede definir como línea de base energética para el IDEn Energía Eléctrica Total (EET) el valor promedio del consumo de los últimos 12 meses correspondiente a 89.409 kWh. Opcionalmente, se puede tomar como línea de base el valor de consumo de cada mes, para que en la evaluación del indicador se compare con el mismo mes que se está evaluando.")]),_vm._m(3),_vm._m(4),_c('p',{staticClass:"mb-5",attrs:{"data-aos":"fade-down"}},[_vm._v("Debido a lo anterior, el equipo de gestión de la energía decide establecer un IDEn de tipo cociente de valores medidos, con alcance organizacional, denominado como EP_T donde sea posible la creación de un modelo que permita relacionar el consumo de energía total de la planta y la producción t